	export default {
		data() {
			return {
				moreMovies: [],
                indexTab: 0,
                num: 2,
                modleNum: 0,
                moreFlag: false,
                loadMoreText: "加载中...",
                showLoadMore: false,
                showFalg: true
			}
		},
		methods: {
			moreData() {
				uni.request({
					url: this.websiteUrl + '/api/home/more',
					method: 'GET',
					data: {
                        typeId:this.indexTab,
                        modleId: this.modleNum,
                        currPage:this.num
                    },
					success: res => {
						if (res.data.length > 0) {
							let data = []
							for (var i = 0; i < res.data.length; i++) {
								data.push(res.data[i])
							}
							this.moreMovies = this.moreMovies.concat(data);
							this.moreFlag = true;
                            this.num++;
						} else {
							this.moreFlag = false;
							this.showFalg = false;
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
            movieTo(e){
                let movieId = e.currentTarget.dataset.id;
                uni.navigateTo({
                    url: '/pages/playPage/playPage?vodId='+movieId,
                    animationType: 'slide-in-right',
                    animationDuration: 300
                });
            }
		},
		onLoad(option) {
            this.indexTab = option.index;
            this.modleNum = option.id
			uni.setNavigationBarTitle({
				title: option.title
			});
            uni.request({
                url: this.websiteUrl + '/api/home/more',
                method: 'GET',
                data: {
                    typeId: option.index,
                    modleId: option.id,
                    currPage: 1
                },
                success: res => {
                    this.moreMovies = res.data;
                },
                fail: () => {},
                complete: () => {}
            });
		},
        onUnload() {
        	this.data = [],
        	this.showLoadMore = false;
        	this.showFalg= true;
        	this.moreFlag= false;
        },
        onReachBottom() {
        	console.log("onReachBottom");
        	if (!this.showFalg) {
        		this.loadMoreText = "我是有底线的!"
        		return;
        	}
        	this.showLoadMore = true;
        	setTimeout(() => {
        		this.moreData();
        	}, 300);
        }
	}
